<template>
  <div id="app">
    <v-app id="inspire">
      <v-row justify="center">
        <v-dialog v-model="$store.getters.journalCreatePopUp" persistent max-width="600px">
          <v-card class="pa-4">
            <v-card-title>
              <span class="headline">Journal Entry</span>
              <span class="icon">
                <v-icon
                 
                  :color="$vuetify.theme.themes[$store.getters.usedTheme ].primary"
                  @click="savePopUp"
                >mdi-floppy</v-icon>
                <v-divider color="#EEEEEE" class="mx-2" vertical></v-divider>
                <v-icon  @click="closeCreatePopUp"> mdi-close</v-icon>
              </span>
            </v-card-title>

            <v-stepper v-model="e6" vertical>
              <v-stepper-step :editable="canEdit" :complete="e6 > 1" step="1">Choose Strategy</v-stepper-step>
              <v-stepper-content step="1"></v-stepper-content>

              <v-stepper-step :editable="canEdit" :complete="e6 > 2" step="2">Check Trade Setup</v-stepper-step>
              <v-stepper-content step="2">
                <journal-create-dialog-checklist></journal-create-dialog-checklist>
              </v-stepper-content>

              <v-stepper-step :editable="canEdit" :complete="e6 > 3" step="3">Enter Journal</v-stepper-step>
              <v-stepper-content step="3">
                <journal-form v-bind:parentvalue="{}" v-on:send-to-parent="updateFromChild"></journal-form>
              </v-stepper-content>

              <v-stepper-step :editable="canEdit" :complete="e6 > 4" step="4">Add History</v-stepper-step>
              <v-stepper-content step="4">
                <journal-history v-bind:parentvalue="{}" v-on:send-to-parent="updateHistoryChild"></journal-history>
              </v-stepper-content>
            </v-stepper>
          </v-card>
        </v-dialog>
      </v-row>
    </v-app>
  </div>
</template>

<script>
import axios from "axios";
import JournalForm from "./Journal/JournalForm";
import JournalHistory from "./Journal/JournalHistory";
import JournalCreateDialogChecklist from "./Journal/JournalCreateDialogChecklist";
export default {
  components: {
    JournalForm,
    JournalHistory,
    JournalCreateDialogChecklist
  },
  name: "journal-dialog-create",
  data() {
    return {
      e6: 1,
      canEdit: true,
      journalFormValues: {},
      journalHistoryValues: [],
      formElliotwave: false,
      form: false,
      awesome: false,
      myValues: {}
    };
  },
  methods: {
    updateFromChild: function(myJson) {
      this.journalFormValues = myJson;
    },
    updateHistoryChild: function(myJson) {
      this.journalHistoryValues = myJson;
    },

    closeCreatePopUp: function() {
      this.$store.commit("setJournalCreatePopUp", false);
    },
    savePopUp: function() {
      this.myValues = this.journalFormValues;
      this.myValues.historyMAP = this.journalHistoryValues.historyMap;
      this.myValues.typ = "create";
      axios.post("/api/JournalUpdateCreate", this.myValues).then(
        response => {
          // eslint-disable-next-line
          console.log(response);
          this.$store.commit("setJournalUpdateNeeded", true);
          this.$store.commit("setJournalCreatePopUp", false);
        },
        error => {
          // eslint-disable-next-line
          console.log(error);
        }
      );
    },
    getData() {      let settings = this.$store.getters.getSettings
      axios
        .get("/api/overview", {
          params: {
            options: {
               sender: "JournalCreateDialog.vue",
              lineChart: {
                limit: settings.Dashboard.LinechartLimit,
                order: this.lineChartOrder
              }
            }
          }
        }).then(
        response => {
          // eslint-disable-next-line
          console.log(response);
        },
        error => {
          // eslint-disable-next-line
          console.log(error);
        }
      );
    }
  }
};
</script>

<style scoped>
.icon {
  margin-left: auto;
  margin-right: 0;
}
</style>