<template>
  <v-app>
    <v-row justify="center">
      <v-dialog
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        v-model="$store.getters.journalInfoPopUp"
        persistent
        max-width="600px"
      >
        <v-card>
          <v-toolbar dark color="white">
            <v-toolbar-items>
              <v-icon
                :color="$vuetify.theme.themes[$store.getters.usedTheme].secondary"
                text
                @click="closeInfoPopUp"
              >mdi-close</v-icon>
            </v-toolbar-items>
            <v-divider color="#EEEEEE" class="mx-2" vertical></v-divider>

            <v-toolbar-title class="text-center black--text">Trade Info</v-toolbar-title>

            <v-divider color="#EEEEEE" class="mx-2" vertical></v-divider>

            <span
              class="subheading text-center black--text"
            >ID: {{$store.getters.journalInfoPopUpId}}</span>
            <v-spacer></v-spacer>
            <v-divider color="#EEEEEE" class="mx-2" vertical></v-divider>
            <v-toolbar-items>
              <v-icon
                @click="savePopUp"
                :color="$vuetify.theme.themes[$store.getters.usedTheme].secondary"
              >mdi-floppy</v-icon>
            </v-toolbar-items>
            <v-divider color="#EEEEEE" class="mx-2" vertical></v-divider>
            <v-toolbar-items>
              <v-icon
                :value="$store.getters.journalInfoPopUpId"
                @click="deleteTrade"
                color="red"
              >mdi-delete-outline</v-icon>
            </v-toolbar-items>
          </v-toolbar>

          <v-container>
            <v-row justify="center">
              <v-col cols="12" sm="6" md="4">
                <circle-card
                  circle-size="100"
                  circle-value="25"
                  :circle-color="$vuetify.theme.themes[$store.getters.usedTheme].primary"
                >
                  <template slot="content">{{journal.totalBetrag}}</template>
                </circle-card>
              </v-col>
            </v-row>
          </v-container>
          <!-- 
            Forms und Stuff
          -->

          <journal-form v-bind:parentvalue="parentvalue" v-on:send-to-parent="updateFromChild"></journal-form>

          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <p class="text-center font-weight-thin">
                  <v-rating
                    :background-color="$vuetify.theme.themes[$store.getters.usedTheme ].primary"
                    :color="$vuetify.theme.themes[$store.getters.usedTheme].primary"
                    v-model="buyexecution"
                  ></v-rating>Buy Execution Rating
                </p>
              </v-col>

              <v-col cols="12" sm="6" md="3">
                <p class="text-center font-weight-thin">
                  <v-rating
                    :color="$vuetify.theme.themes[$store.getters.usedTheme].primary"
                    v-model="sellexecution"
                  ></v-rating>Sell Execution Rating
                </p>
              </v-col>
            </v-row>
          </v-container>
          <v-container>
            <v-card shaped outlined flat>
            <v-row  >
                <v-col cols="12" sm="3" md="3" v-for="(item, index) in pictures" :key="index">
                  <img
                    class="grey lighten-2 ma-1"
                    v-img:group-1
                    :src="'http://192.168.178.111:8080/images/'+item"
                  />
                </v-col>
            </v-row>
            </v-card>
          </v-container>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <journal-picture-upload
                  v-if="calculationDone"
                  :journalId="$store.getters.journalInfoPopUpId"
                  :historyId="singleHistory"
                  uploadurl="/api/picUpload"
                  geturl="dummy"
                ></journal-picture-upload>
              </v-col>
            </v-row>
          </v-container>
          <journal-history   v-on:send-to-parent="updateHistoryChild"></journal-history>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <!-- History -->
                <h4 class="text-center">History</h4>
                <v-expansion-panels accordion>
                  <v-expansion-panel v-for="(ID_History,i) in history" :key="i">
                    <v-expansion-panel-header>
                      <v-row dense>
                        <v-col cols="12" sm="11" md="11">{{ID_History.ID_History}}</v-col>
                        <v-col cols="12" sm="1" md="1">
                          <v-icon
                            :value="ID_History.ID_History"
                            @click="unlinkHistory"
                            color="red"
                          >mdi-delete-outline</v-icon>
                        </v-col>
                      </v-row>
                    </v-expansion-panel-header>
                    <v-expansion-panel-content>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</v-expansion-panel-content>
                  </v-expansion-panel>
                </v-expansion-panels>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-dialog>
    </v-row>
  </v-app>
</template>

<script>
import CircleCard from "./../components/Circle/CircleCard";
import JournalForm from "./Journal/JournalForm";
import JournalHistory from "./Journal/JournalHistory";
import JournalPictureUpload from "./Journal/JournalPictureUpload";

import axios from "axios";
export default {
  components: {
    CircleCard,
    JournalForm,
    JournalHistory,
    JournalPictureUpload
  },
  name: "journal-info-dialog",
  data() {
    return {
      parentvalue: {
        symbol: "",
        period: "",
        strategy: "",

        freitext1: "",
        freitext2: "",
        freitext3: "",
        freitext4: "",

        fehler1: "",
        fehler2: "",
        fehler3: "",
        fehler4: "",

        buyfib1: "",
        buyfib2: "",
        buyfib3: "",
        buyfib4: "",

        sellfib1: "",
        sellfib2: "",
        sellfib3: "",
        sellfib4: "",


        sellfib1Result : "",
        sellfib2Result : "",
        sellfib3Result : "",
        sellfib4Result : "",

        risklevel: "",
        risktotal: "",
        bemerkung: ""
      },
      pictureID: null,
      item: null,
      calculationDone: false,
      pictures: [],
      journalFormValues: {},
      journalHistoryValues: [],
      sellexecution: 0,
      buyexecution: 0,
      history: [],
      journal: {},
      singleHistory: "00"
    };
  },
  computed: {
    dialogOpen() {
      return this.$store.getters.journalInfoPopUp;
    },
    dialogOpenUpdate() {
      return this.$store.getters.journalInfoPopUpUpdateNeeded;
    }
  },
  watch: {
    dialogOpen: function() {
      if (this.$store.getters.journalInfoPopUpId != null && this.$store.getters.journalInfoPopUpTyp != "newtrade") {
        this.getData();
      }
      if(this.$store.getters.journalInfoPopUpTyp == "newtrade"){
        this.prefill()
      }
    },
    dialogOpenUpdate: function() {
      if (this.$store.getters.journalInfoPopUpId != null && this.$store.getters.journalInfoPopUpTyp != "newtrade") {
        this.getData();
      }
      if(this.$store.getters.journalInfoPopUpTyp == "newtrade"){
        this.prefill()
      }
    }
  },
  created() {},
  mounted() {},
  methods: {
    closeFullSize: function() {
      this.$store.commit("setJournalInfoPicturePopUp", false);
    },
    fullsizeimage: function(value) {
      this.pictureID = value.target.value;
      this.$store.commit("setJournalInfoPicturePopUp", true);
    },
    updateFromChild: function(myJson) {
      this.journalFormValues = myJson;
    },
    updateHistoryChild: function(myJson) {
    
      this.journalHistoryValues = myJson;
    },

    savePopUp: function() {
      this.myValues = this.journalFormValues;
      this.myValues.historyMAP = this.$store.getters.historyMap;
     
      this.myValues.buyexecution = this.buyexecution;
      this.myValues.sellexecution = this.sellexecution;
     
      if(this.$store.getters.journalInfoPopUpTyp == "newtrade"){
        this.myValues.typ = "create";
         this.myValues.journalId = null;
      }else{
        this.myValues.typ = "update";
         this.myValues.journalId = this.$store.getters.journalInfoPopUpId;
      }

      axios.post("/api/JournalUpdateCreate", this.myValues).then(
        response => {
          // eslint-disable-next-line
          console.log(response)
          this.$store.commit("setJournalUpdateNeeded", true);
          this.$store.commit("setJournalInfoPopUpUpdateNeeded", true);
          this.$store.commit("setJournalCreatePopUp", false);
          this.historyMap = [];
          this.closeInfoPopUp()
        },
        error => {
          // eslint-disable-next-line
          console.log(error);
        }
      );

     
    },

    unlinkHistory: function(value) {
      if (confirm("Do you really want to delete?")) {
        axios
          .post("/api/unlinkHistory", {
            historyId: value.target.value,
            journalId: this.$store.getters.journalInfoPopUpId
          })
          .then(
            response => {
              // eslint-disable-next-line
              console.log(response);

              this.$store.commit("setJournalInfoPopUpUpdateNeeded", true);
            },
            error => {
              // eslint-disable-next-line
              console.log(error);
            }
          );
      }
    },
    deleteTrade: function(value) {
      if (confirm("Do you really want to delete?")) {
        this.$store.commit("setJournalUpdateNeeded", true);
        axios
          .post("/api/deleteSingleJournal", {
            journalId: value.target.value
          })
          .then(
            response => {
              // eslint-disable-next-line
              console.log(response);
            },
            error => {
              // eslint-disable-next-line
              console.log(error);
            }
          );
        this.$store.commit("setJournalUpdateNeeded", true);
        this.$store.commit("setJournalInfoPopUp", { status: false, id: null, typ:""});
      }
    },
    closeInfoPopUp: function() {
      this.setEmpty()
      this.$store.commit("setHistoryMap",[{}] );
      this.$store.commit("setJournalInfoPopUp", { status: false, id: null, typ:"", produkt:"" });
    },
    setSingleHistory() {
      let id = "0";
      if (this.history[0] != undefined) {
        id = this.history[0].ID_History;
      }
      this.singleHistory = id;
      this.calculationDone = true;
    },

prefill() {
 this.$store.commit("setHistoryMap",[{histID: this.$store.getters.journalInfoPopUpId}] );

 this.parentvalue.symbol = this.$store.getters.journalInfoPopUpProdukt

 axios
        .get("/api/getSinglePictures", {
          params: {
            id: this.$store.getters.journalInfoPopUpId
          }
        })
        .then(
          response => {  
            this.pictures = response.data.journalPictures;
          },
          error => {
            // eslint-disable-next-line
            console.log(error);
          }
        );
},
setEmpty(){
            this.pictures = ""
            this.history = ""
            this.journal= ""
            this.journalFormValues = ""
            this.buyexecution = 0
            this.sellexecution = 0

      this.parentvalue = {
        symbol: "",
        period: "",
        strategy: "",

        freitext1: "",
        freitext2: "",
        freitext3: "",
        freitext4: "",

        fehler1: "",
        fehler2: "",
        fehler3: "",
        fehler4: "",

        buyfib1: "",
        buyfib2: "",
        buyfib3: "",
        buyfib4: "",

        sellfib1: "",
        sellfib2: "",
        sellfib3: "",
        sellfib4: "",


        sellfib1Result : "",
        sellfib2Result : "",
        sellfib3Result : "",
        sellfib4Result : "",

        risklevel: "",
        risktotal: "",
        bemerkung: ""
      }
      






},
    getData() {
      axios
        .get("/api/getSingleJournal", {
          params: {
            journalid: this.$store.getters.journalInfoPopUpId
          }
        })
        .then(
          response => {
            this.pictures = response.data.journalPictures;
            this.history = response.data.history;
            this.journal = response.data.journal;
            this.parentvalue.symbol = this.journal.SYMBOL;
            this.parentvalue.period = this.journal.PERIOD;
            this.parentvalue.strategy = this.journal.STRATEGY;
            this.parentvalue.myentry = this.journal.MYENTRY;
            this.parentvalue.myentryline = this.journal.MYENTRYLINE;
            this.parentvalue.myexit = this.journal.MYEXIT;

            this.parentvalue.freitext1 = this.journal.FREITEXT1;     
            this.parentvalue.freitext2 = this.journal.FREITEXT2;
            this.parentvalue.freitext3 = this.journal.FREITEXT3;    
            this.parentvalue.freitext4 = this.journal.FREITEXT4;  


            this.parentvalue.fehler1 = this.journal.FEHLER1;     
            this.parentvalue.fehler2 = this.journal.FEHLER2;
            this.parentvalue.fehler3 = this.journal.FEHLER3;    
            this.parentvalue.fehler4 = this.journal.FEHLER4; 


            this.parentvalue.buyfib1 = this.journal.BUYFIB1;     
            this.parentvalue.buyfib2 = this.journal.BUYFIB2;
            this.parentvalue.buyfib3 = this.journal.BUYFIB3;    
            this.parentvalue.buyfib4 = this.journal.BUYFIB4; 


            this.parentvalue.sellfib1 = this.journal.SELLFIB1;     
            this.parentvalue.sellfib2 = this.journal.SELLFIB2;
            this.parentvalue.sellfib3 = this.journal.SELLFIB3;    
            this.parentvalue.sellfib4 = this.journal.SELLFIB4; 

            this.parentvalue.sellfib1Result = this.journal.SELLFIB1RESULT;     
            this.parentvalue.sellfib2Result = this.journal.SELLFIB2RESULT;
            this.parentvalue.sellfib3Result = this.journal.SELLFIB3RESULT;    
            this.parentvalue.sellfib4Result = this.journal.SELLFIB4RESULT; 

            this.parentvalue.risklevel = this.journal.RISKLEVEL;
            this.parentvalue.risktotal = this.journal.RISKTOTAL;    
            this.parentvalue.bemerkung = this.journal.BEMERKUNG; 

            this.journalFormValues = this.parentvalue
            this.buyexecution = this.journal.BUYEXECUTIONRATING;
            this.sellexecution = this.journal.SELLEXECUTIONRATING;
            this.$store.commit("setJournalInfoPopUpUpdateNeeded", false);
            this.setSingleHistory();
          },
          error => {
            // eslint-disable-next-line
            console.log(error);
          }
        );
    }
  }
};
</script>

<style lang="scss" scoped>
.iconcenter {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  margin-top: 0;
  margin-bottom: auto;
}
.imgclass {
  font-size: 1em;
  height: 150px;
  width: 150px;
}
.imgFullsizeclass {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
</style>