import Vue from 'vue'
import Router from 'vue-router'

import DashboardLayout from "@/pages/Layout/DashboardLayout.vue";
//import LoginLayout from "@/pages/Layout/LoginLayout.vue";
import Dashboard from "@/pages/Dashboard.vue";
import Journal from "@/pages/Journal.vue";
import Trades from "@/pages/Trades/Trades.vue";
import Auswertung from "@/pages/Auswertung/Auswertung.vue";
import Upload from "@/pages/Upload.vue";
import Login from "@/pages/Login.vue";
import Statistik from "@/pages/Statistik/DayCalender.vue";
import Log from "@/pages/Log/Log.vue";
Vue.use(Router)


export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/",
      component: DashboardLayout,
      redirect: "/dashboard",
      children: [
        {
          path: "dashboard", 
          name: "Dashboard",
          component: Dashboard
        },
        {
          path: "journal",
          name: "Journal",
          component: Journal
        },
        {
          path: "log",
          name: "log",
          component: Log
        },
        {
          path: "auswertung",
          name: "auswertung",
          component: Auswertung
        },
        {
          path: "trades",
          name: "trades",
          component: Trades
        },
        {
          path: "statistik",
          name: "statistik",
          component: Statistik
        },
        

        {
          path: "upload",
          name: "Upload",
          component: Upload
        }


      ]
    }, {
      path: "/login",
      component: Login,
      name: "Login"
    }
  ]
})

