<template>
  <div class="content">
    <v-expansion-panels accordion>
      <v-expansion-panel>
        <v-expansion-panel-header>Datepicker</v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col cols="12" sm="6">
              <v-row>
                <v-col cols="12" sm="6">
                  <v-date-picker v-model="dates" range></v-date-picker>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-btn @click="getData()">Reload</v-btn>
                </v-col>
              </v-row>
            </v-col>
            <v-col></v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-row>
      <v-col cols="12">
        <div>
          <md-table
            v-model="journalItems"
            :table-header-color="$vuetify.theme.themes[$store.getters.usedTheme ].primary"
          >
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="Date"></md-table-cell>
              <md-table-cell md-label="Symbol">{{ item.SYMBOL }}</md-table-cell>
              <md-table-cell md-label="Strategy">{{ item.STRATEGY }}</md-table-cell>
              <md-table-cell md-label="Period">{{ item.PERIOD }}</md-table-cell>

              <md-table-cell md-label="Status"></md-table-cell>
              <md-table-cell md-label="Win/Loss">{{ item.TOTALSUM }}</md-table-cell>
              <md-table-cell>
                <v-icon
                  :value="item.ID"
                  @click="openInfoPopUp"
                  medium
                  :color="$vuetify.theme.themes[$store.getters.usedTheme ].primary"
                >mdi-information-outline</v-icon>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </div>
      </v-col>
    </v-row>

    <v-container fluid>
      <v-btn
        @click="openCreatePopUp"
        :color="$vuetify.theme.themes[$store.getters.usedTheme ].primary"
        fab
        bottom
        fixed
        right
      >
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      <journal-create-dialog></journal-create-dialog>
      <journal-info-dialog></journal-info-dialog>
    </v-container>
  </div>
</template>

<style scoped>
</style>        

<script>
import axios from "axios";
import JournalCreateDialog from "./JournalCreateDialog";
import JournalInfoDialog from "./JournalInfoDialog";
export default {
  components: {
    JournalCreateDialog,
    JournalInfoDialog
  },
  name: "Dashboard",
  data() {
    return {
      dates: [],
      journalItems: [{}],
      theme: "light",
      dialog: false
    };
  },

  computed: {
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
    updateNeeded() {
      return this.$store.getters.journalUpdateNeeded;
    }
  },
  watch: {
    updateNeeded: function() {
      if (this.$store.getters.journalUpdateNeeded != null) {
        this.getData();
      }
    }
  },
  created() {
    this.interval = setInterval(() => this.getData(), 60000);
    this.dates[0] = new Date();
    this.dates[0].setDate(this.dates[0].getDate() - 7);
    this.dates[0] = this.dates[0].toISOString().split("T")[0];
    this.dates[1] = new Date().toISOString().split("T")[0];
    console.log(this.dates[0]);
  },
  mounted() {
    this.getData();
  },
  methods: {
    openCreatePopUp: function() {
      this.$store.commit("setJournalCreatePopUp", {
        status: true,
        id: null
      });
    },
    openInfoPopUp: function(value) {
      this.$store.commit("setJournalInfoPopUp", {
        status: true,
        id: value.target.value,
        produkt: value.target.produkt
      });
    },
    getData() {
      this.$store.commit("setJournalUpdateNeeded", false);

      let time1 = this.dates[0];
      let time2;
      if (this.dates[1] == null || this.dates[1] == undefined) {
        time2 = this.dates[0];
      } else {
        time2 = this.dates[1];
      }

      let d1 = new Date(time1);
      let d2 = new Date(time2);

      if (d2 < d1) {
        let temp = time1;
        time1 = time2;
        time2 = temp;
      }

      axios
        .get("/api/journal", {
          params: {
            startTime: time1,
            endTime: time2
          }
        })
        .then(
          response => {
            // eslint-disable-next-line
            this.journalItems = response.data;
          },
          error => {
            // eslint-disable-next-line
            console.log(error);
          }
        );
    }
  }
};
</script>






