<template>
  <div class="content">


        <v-expansion-panels accordion>
      <v-expansion-panel>
        <v-expansion-panel-header>Datepicker</v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col cols="12" sm="6">
              <v-row>
                <v-col cols="12" sm="6">
                  <v-date-picker v-model="dates" range></v-date-picker>
                </v-col>
                          <v-col cols="12" sm="6">
            <v-radio-group v-model="filter" :mandatory="false">
              <v-radio label="Unlinked" value="empty"></v-radio>
              <v-radio label="Linked" value="linked"></v-radio>
              <v-radio label="All" value="all"></v-radio>
            </v-radio-group>
          </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-btn @click="getData()">Reload</v-btn>
                </v-col>
              </v-row>
            </v-col>
            <v-col></v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>




  </div>
</template>

<style scoped>
</style>        

 
<script>
//import axios from "axios";
import axios from "axios";


export default {
  components: { },
  name: "Dashboard",
  data() {
    return {
      dates: [],
      tradeItems: [{}],
      theme: "light",
      dialog: false,
      filter: "empty"
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
    updateNeeded() {
      return this.$store.getters.journalInfoPopUpUpdateNeeded;
    }
  },

  watch: {
    updateNeeded: function() {
      console.log("hier");
      if (this.$store.getters.journalInfoPopUpUpdateNeeded != null) {
        this.getData();
      }
    }
  },
  created() {
    (this.dates[0] = new Date().toISOString().split("T")[0]),
      (this.dates[1] = new Date().toISOString().split("T")[0]);
    //this.interval = setInterval(() => this.getData(), 60000);
  },
  mounted() {
    this.getData();
  },

  methods: {
    openCreatePopUp: function(auftragsnr, produkt) {
      this.$store.commit("setJournalInfoPopUp", {
        status: true,
        id: auftragsnr,
        typ: "newtrade",
        produkt: produkt
      });
    },

    getData() {
      let time1 = this.dates[0];
      let time2;
      if (this.dates[1] == null || this.dates[1] == undefined) {
        time2 = this.dates[0];
      } else {
        time2 = this.dates[1];
      }

      let d1 = new Date(time1);
      let d2 = new Date(time2);

      if (d2 < d1) {
        let temp = time1;
        time1 = time2;
        time2 = temp;
      }

      axios
        .get("/api/getRiskBasedResult", {
          params: {
            filter: this.filter,
            startTime: time1,
            endTime: time2
          }
        })
        .then(
          response => {
            // eslint-disable-next-line
            console.log( response.data)
          },
          error => {
            // eslint-disable-next-line
            console.log(error);
          }
        );
    }
  }
};
</script>
