                 ></v-select>
              </v-col>

            </v-row>
          </template>
        </subheader-card>
          </v-col>
 
          <v-col class="d-flex" cols="12" sm="12">
        <subheader-card>
          <template slot="header">Basic Select</template>
          <template slot="content">
            <v-row dense>
              <v-col class="d-flex" cols="12" sm="12">Risiko Mangament</v-col>
              <v-col class="d-flex mt-n3" cols="12" sm="6">
                <v-text-field
