          <template slot="header">Basic Select</template>
          <template slot="content">
            <v-row dense>
              <v-col class="d-flex" cols="12" sm="12">Fehler</v-col>
              <v-col class="d-flex mt-n3" cols="12" sm="6">
                 <v-select
                  dense
                  @change="sendToParent"
                         :items="selectFehler1"
                  v-model="mydata.fehler1"
                  label="fehler1"
                  single-line
                ></v-select>
              </v-col>

              <v-col class="d-flex mt-n3" cols="12" sm="6">
                <v-select
