</template>

<script>
import SubheaderCard from "./../../components/Cards/SubheaderCard";
export default {
  components: { SubheaderCard },
  name: "journal-form",
  props: {
    parentvalue: {
      symbol: {
        type: String,
        default: ""
      },
      period: {
        type: String,
        default: ""
      },
      myentry: {
        type: String,
        default: ""
      },
      myexit: {
        type: String,
        default: ""
      },
      strategy: {
        type: String,
        default: ""
      },

      freitext1: {
        type: String,
        default: ""
      },
      freitext2: {
        type: String,
        default: ""
      },
      freitext3: {
        type: String,
        default: ""
      },
      freitext4: {
        type: String,
        default: ""
      },

      fehler1: {
        type: String,
        default: ""
      },
      fehler2: {
        type: String,
        default: ""
      },
      fehler3: {
        type: String,
        default: ""
      },
      fehler4: {
        type: String,
        default: ""
      },

      sellfib1: {
        type: String,
        default: ""
      },
      sellfib2: {
        type: String,
        default: ""
      },
      sellfib3: {
        type: String,
        default: ""
      },
      sellfib4: {
        type: String,
        default: ""
      },

      sellfib1Result: {
        type: String,
        default: ""
      },
      sellfib2Result: {
        type: String,
        default: ""
      },
      sellfib3Result: {
        type: String,
        default: ""
      },
      sellfib4Result: {
        type: String,
        default: ""
      },

      buyfib1: {
        type: String,
        default: ""
      },
      buyfib2: {
        type: String,
        default: ""
      },
      buyfib3: {
        type: String,
        default: ""
      },
      buyfib4: {
        type: String,
        default: ""
      },
      risklevel: {
        type: String,
        default: ""
      },
      risktotal: {
        type: String,
        default: ""
      },
      bemerkung: {
        type: String,
        default: ""
      }
    }
  },


  data() {
    return {
      myentry: ["","Durchbruch", "Umkehrkerze" , "Fib",  "Fire-Forget"  ,  "Other"],
      myentryline: ["","Punkt 2", "Tageslinie" ,  "Other"],
      myexit: ["","Trend", "Bewegung", "Ausbruch" , "TP/SL Only" , "Other" ,  "Fire-Forget"],
      selectBuyfib: ["","23", "38" , "50" , "61"],
      selectSellfib:  ["","61", "100" , "161" , "200" , "261" , "423" , "581"],
      selectSellfibResult:  ["","Erreicht", "Nicht Erreicht" , "Selbst Raus" , "Voller Stop Loss" , "SL Nachgezogen" ],
      selectFehler1: ["","Einstieg - Fear of Missing out", "Nicht auf Fib gewartet"],
      selectFehler2: ["","Ausstieg - Der Dreht schon" , "SL berhastet"],
      selectStrategyItems: ["", "Markttechnik", "Moving Average", "EW" , "Scalping" , "Wirmachentrader", "Spielerrei" ,  "Fire-Forget"],
      selectPeriodItems: ["",
        "1W",
        "1D",
        "4H",
        "1H",
        "30M",
        "15M",
        "10M",
        "5M",
        "3M",
        "1M"
      ]
    };
  },
  computed: {
    mydata: function() {
      return this.parentvalue;
    }
  },
  methods: {
    sendToParent: function() {
      this.$emit("send-to-parent", this.parentvalue);
    },

    removeItemOfMap: function(value) {
      let val = value.target.value;
      var index = this.historyMap.findIndex(function(item) {
        return item.histID === val;
      });
      if (index > -1) {
        this.historyMap.splice(index, 1);
      }
    },
    addToMapp: function() {
      if (this.historyID != "") {
        let histID = this.historyID;
        let temp = {
          histID: histID,
          calories: 237
        };
        this.historyMap.push(temp);
        console.log(this.historyMap);
        this.historyID = "";
      }
    },
    changedStrategy: function(value) {
      if ("Markttechnik" == value) {
        console.log("");
      }
      console.log(value);
      this.sendToParent();
    },
    closeCreatePopUp: function() {
      this.$store.commit("setJournalCreatePopUp", false);
    }
  }
};
</script>

