<template>
  <div class="content">
    <v-row>
      <v-col cols="sm-6 xs-12">
        <bar-chart
          v-if="options.loaded"
          :chart-data="datacollectionweek"
          :options="options"
        ></bar-chart>
      </v-col>
      <v-col cols="sm-6 xs-12">
        <bar-chart
          v-if="options.loaded"
          :chart-data="datacollectionday"
          :options="options"
        ></bar-chart>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="sm-12 xs-12">
        <line-chart
          v-if="options.loaded"
          :chart-data="linedatacollection"
          :options="lineoptions"
        ></line-chart>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <stats-card-wrapper
          :data-background-color="
            $vuetify.theme.themes[$store.getters.usedTheme].primary
          "
        >
          <template slot="header">
            <md-icon>Daily</md-icon>
          </template>

          <template slot="content">
            <v-row>
              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Count</template>
                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ dailyTradeCount }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>

              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Sum</template>

                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ dailySum }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>

              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Sum</template>

                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ dailyWinRate }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>
            </v-row>
          </template>
        </stats-card-wrapper>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12">
        <stats-card-wrapper
          :data-background-color="
            $vuetify.theme.themes[$store.getters.usedTheme].primary
          "
        >
          <template slot="header">
            <md-icon>Weekly</md-icon>
          </template>
          <template slot="content">
            <v-row>
              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Count</template>
                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ weeklyTradeCount }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>

              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Sum</template>

                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ weeklySum }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>

              <v-col cols="sm-4 xs-12">
                <stats-card
                  :data-background-color="
                    $vuetify.theme.themes[$store.getters.usedTheme].primary
                  "
                >
                  <template slot="header">Sum</template>

                  <template slot="content">
                    <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="
                        $vuetify.theme.themes[$store.getters.usedTheme].primary
                      "
                    >
                      <template slot="content">{{ weeklyWinRate }}</template>
                    </circle-card>
                  </template>
                </stats-card>
              </v-col>
            </v-row>
          </template>
        </stats-card-wrapper>

        <stats-card-wrapper
          :data-background-color="
            $vuetify.theme.themes[$store.getters.usedTheme].primary
          "
        >
          <template slot="header">Daily</template>
          <template slot="content">
            <DashboardPositionTable></DashboardPositionTable>
          </template>
        </stats-card-wrapper>
      </v-col>
    </v-row>
  </div>
</template>
             

 

<script>
import axios from "axios";
import StatsCard from "./../components/Cards/StatsCard";
import StatsCardWrapper from "./../components/Cards/StatsCardWrapper";
import DashboardPositionTable from "./DashboardPositionTable";
import CircleCard from "./../components/Circle/CircleCard";
import BarChart from "./BarChart.js";
import LineChart from "./LineChart.js";

export default {
  components: {
    StatsCard,
    CircleCard,
    StatsCardWrapper,
    DashboardPositionTable,
    BarChart,
    LineChart
  },

  name: "Dashboard",
  data() {
    return {
      lineChartLimit: 10,
      lineChartOrder: "day",
      myPrimary: "#ffddcc",
      dailyTradeCount: 0,
      dailySum: -10,
      dailyWinRate: 100,
      weeklyTradeCount: 0,
      weeklySum: 0,
      weeklyWinRate: 0,
      datacollectionweek: null,
      datacollectionday: null,
      chartSum: null,
      options: {
        responsive: true,
        maintainAspectRatio: false,
        loaded: false
      },
      linedatacollection: null,
      lineoptions: {
        responsive: true,
        maintainAspectRatio: false,
        loaded: false,
        scales: {
          yAxes: [
            {
              ticks: {
                suggestedMin: 50,
                suggestedMax: 100,
                beginAtZero: true,
                bezierCurve: false
              }
            }
          ]
        }
      }
    };
  },
  computed: {
    cssPrimary() {
      return this.$vuetify.theme.themes[this.$store.getters.usedTheme].primary;
    }
  },
  created() {
    this.interval = setInterval(() => this.getData(), 60000);
  },
  mounted() {
    this.getData();
  },
  methods: {
    reducer(accumulator, currentValue) {
      if (accumulator != undefined && currentValue != undefined) {
        return accumulator + currentValue;
      } else {
        return 0;
      }
    },
    fillData() {
      this.datacollectionweek = {
        labels: ["-4", "-3", "-2", "-1", "Current"],
        datasets: [
          {
            label: this.chartSumWeek.reduce(this.reducer),
            backgroundColor: this.$vuetify.theme.themes[
              this.$store.getters.usedTheme
            ].primary,
            data: this.chartSumWeek
          }
        ]
      };

      (this.linedatacollection = {
        labels: this.linechartLabels,
        datasets: [
          {
            backgroundColor: this.$vuetify.theme.themes[
              this.$store.getters.usedTheme
            ].primary,

            data: this.linechartData,

            label: "Data One"
          }
        ]
      }),
        (this.datacollectionday = {
          labels: ["Mo", "Di", "Mi", "Do", "Fr"],
          datasets: [
            {
              label: this.chartSumDay.reduce(this.reducer),
              backgroundColor: this.$vuetify.theme.themes[
                this.$store.getters.usedTheme
              ].primary,
              data: this.chartSumDay,
              lineTension: 0
            }
          ]
        });

      this.options.loaded = true;
    },

    getData() {

      let settings = this.$store.getters.getSettings

console.log(settings)

      axios
        .get("/api/overview", {
          params: {
            options: {
               sender: "Dashboard.vue",
              lineChart: {
                limit: settings.Dashboard.LinechartLimit,
                order: this.lineChartOrder
              }
            }
          }
        })
        .then(
          response => {
            // eslint-disable-next-line

            this.dailyTradeCount = response.data.daily.tradeCount;
            this.dailySum = response.data.daily.sum;
            this.dailyWinRate = response.data.daily.winRate;
            this.chartSumWeek = response.data.barChartWeek.map(
              value => value.betrag
            );

            this.chartSumDay = response.data.barChartDay.map(
              value => value.betrag
            );
            console.log(this.chartSumDay);

            console.log(response.data.lineChartData);

            this.linechartData = response.data.lineChartData.map(
              value => value.kontostand
            );

            this.linechartLabels = response.data.lineChartData.map(
              value => value.label
            );

            this.weeklyTradeCount = response.data.weekly.tradeCount;
            this.weeklySum = response.data.weekly.sum;
            this.weeklyWinRate = response.data.weekly.winRate;
            this.fillData();
          },
          error => {
            // eslint-disable-next-line
            console.log(error);
          }
        );
    }
  }
};
</script>





