<template>
  <div>
                 <circle-card
                      circle-size="100"
                      circle-value="25"
                      :circle-color="$vuetify.theme.themes[$store.getters.usedTheme].primary"
                    >
                      <template slot="content">{{totalRisk}}</template>
                    </circle-card>

    <md-table v-model="positions" :table-header-color="tableHeaderColor">
      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell md-label="Order Nr">{{ item.ordernr }}</md-table-cell>
        <md-table-cell md-label="Bought">{{ item.bought }}</md-table-cell>
        <md-table-cell md-label="Count">{{ item.count }}</md-table-cell>
        <md-table-cell md-label="SL">{{ item.sl }}</md-table-cell>
        <md-table-cell md-label="Risk">{{ item.risk }}</md-table-cell>
        <md-table-cell md-label="TP">{{ item.tp }}</md-table-cell>
        <md-table-cell md-label="Profit">{{ item.profit }}</md-table-cell>
      </md-table-row>
    </md-table>
  </div>
</template>

<script>
import axios from "axios";
import CircleCard from "./../components/Circle/CircleCard";
export default {
  name: "simple-table",
  components: {
    CircleCard
    },
  props: {
    tableHeaderColor: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      selected: [],
      tester:10,
      
          totalRisk: 10,
    
      positions: [
        {
          ordernr: "Dakota Rice",
          bought: "$36,738",
          count: "Niger",
          sl: "Oud-Turnhout",
          risk: "Oud-Turnhout",
          tp: "Oud-Turnhout",
          profit: "Oud-Turnhout"
        
        }
      ]
    }
  },
  created() {
    this.interval = setInterval(() => this.getData(), 60000);
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      axios.get("/api/dashboardPositions").then(
        response => {
          // eslint-disable-next-line
          console.log(response.data.position.positions)
        this.positions = response.data.position.positions
        this.totalRisk = response.data.position.total.risk
        },
        error => {
          // eslint-disable-next-line
          console.log(error);
        }
      );
    }
  }

};
</script>
